<template>
  <v-card class="mt-4">
    <v-card-title>
      Solicitações
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Pesquisar"
        single-line
        hide-details
      ></v-text-field>
      <v-btn fab small color="primary ml-4" dark v-on:click="add()" title="Solicitar Cesta Básica">
        <i class="material-icons">add</i>
      </v-btn>
    </v-card-title>
    <v-data-table :headers="headers" :items="solicitacoes" :items-per-page="5">
      <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="update(item.cdSolicitacao)">edit</v-icon>
      <v-icon small @click="remove(item.cdSolicitacao)">delete</v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template>
    </v-data-table>
  </v-card>
</template>

<script>
import DataService from '../service/SolicitacaoDataService';
export default {
  data() {
    return {
      solicitacoes: [],
      search: "",
      headers: [
        { text: '#', align: 'start', sortable: false, value: 'cdSolicitacao',},
        { text: 'Instituição', value: 'instituicao.nome' },
        { text: 'Data', value: 'dtSolicitacao' },
        { text: 'Qtd de Cestas', value: 'qtdCestas' },
        { text: 'Tipo de Cesta', value: 'tpCesta.nome' },
        { text: 'Status', value: 'status' },
        { text: 'Ações', value: 'actions', sortable: false },
      ],
    }
  },
  methods: {
    initialize(){},
    refresh() {
        DataService.retrieveAll().then((response) => {
            this.solicitacoes = response.data;
        });
    },
    add() {
        this.$router.push(`/solicitacoes/add`);
    },
    update(id) {
        this.$router.push(`/solicitacoes/${id}`);
    },
    remove(id) {
        DataService.remove(id)
        .then(() => {
            this.refresh();
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
    },
  },
  created() {
      this.refresh();
  }
}
</script>

